
#nav-header {
  position: fixed;
  display: flex;
  z-index: 100;
  padding: 1vh 1vw 1vh 1vw;
  top: 0vh;
  left: 0vw;
  width: 98%;
  height: 8vh;
  filter: drop-shadow(1px 1px 5px rgba(55,55,55,.5));
  background-color:rgba(248, 248, 248, 1);
}

