#navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  min-width: 70vw;
  padding: 1vh 2.5vw 1vh 2.5vw;
}

#navbar button {
  cursor: pointer;
  width: 12vw;
  height: 100%;
  min-width: 100px;
  margin: unset;
  padding: unset;

  color: #034f84;
  font-weight: bold;
  font-family: sans-serif;
  font-size: calc(8px + 2vmin);

  border: 0 solid;
  border-bottom: 2px solid rgb(40, 52, 130);
  background-color: rgba(255, 255, 255, 0);
}

#navbar button:focus {
  outline: none;
}

#navbar button:hover {
  color: white;
  transition: all .33s;
  border: 1px solid white;
  border-radius: 8px;
  background-color: rgb(40, 52, 130) !important;
}

.nav-menu-button {
  display: none;
  width: 48px;
  height: 48px;
  margin-left: calc(40vw - 20vmin);
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="%23283583"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 17h-12v-2h12v2zm0-4h-12v-2h12v2zm0-4h-12v-2h12v2z"/></svg>');
}

.navbar-menu {
  display: none;
}

@media (max-width: 999px) {
  #nav-header {
    height: fit-content;
    min-height: 48px;
    max-width: 100vw;
    overflow: hidden;
  }
  #header {
    display: flex;
    width: 50%;
    margin-left: 36px;
  }
  #navbar {
    display: none;
  }
  .nav-menu-button {
    display: block;
  }
  .nav-menu-button-open {
    display: none !important;
  }
  #navbar button {
    display: none;
  }
  .navbar-menu-open {
    z-index: 101 !important;
    display: flex !important;
    flex-direction: column !important;
    height: 400px !important;
    width: 100vw !important;
    margin-left: -52.5% !important;
  }
  .navbar-menu-open button {
    display: block !important;
    height: 100px !important;
    width: 100% !important;
    background-color: whitesmoke !important;
  }
}
