
#footer {
  width: 76%;
  z-index: 100;
  display: flex;
  min-height: 10vh;
  overflow: hidden;
  padding: 1vh 12% 1vh 12%;
  justify-content: space-between;
  background-color: #283583;
  filter: drop-shadow(1px 1px 5px rgba(55,55,55,.5));
}

#footer-info {
  width: 50%;
  height: 100%;
}

#footer-info h6, a {
  color: white;
  font-size: 12px;
  margin: 2vh 0 2vh 0;
}

#footer-right {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

