#footer-refs {
  width: 100%;
  height: 100%;
  padding: 0 0 0 20%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

#footer-refs img {
  width: 10vmin;
  max-width: 48px;
}

@media (max-width: 999px) {
  #footer {
    flex-direction: column;
    width: 100%;
    padding: unset;
  }
  #footer-info {
    width: 100%;
    text-align: center;
  }
  #footer-right {
    width: 100%;
  }
  #footer-logo {
    margin-left: unset;
  }
  #footer-refs {
    padding: 0 25% 0 25%;
    width: 50%;
  }
}